import React, { useState, useEffect } from 'react';
import { Col, Form, Input, Row, message, Select } from 'antd';
import BtnGroup from 'components/common/Btn/BtnGroup';
import Btn from 'components/common/Btn/Btn';
import Card from 'components/common/Card/index';
import TableDataEditCell from 'components/common/TableDataEditCell/index';
import {
  getClassTraningScore,
  submitClassTrainingScore,
  updateClassTrainingScore,
} from 'services/studentPortal';
import InputOnlyNumber from 'react-uikit/component/input/InputOnlyNumber';
import { requiredRule } from 'utils/validateRules';

import FormSearch from './FormSearch';

import './styles.scss';
import { getQueryParams } from '../../react-uikit/utils/urlUtil';
import { getSocialTaskTypes } from '../../services/socialTaskType';

export default params => {
  const { practiceTypeId } = params;
  const { classId } = getQueryParams();
  const [yearEduId, setYearEduId] = useState(null);
  const [semesterId, setSemesterId] = useState(null);
  const [formError, setFormError] = useState(null);
  const [dataTableReq, setDataTableReq] = useState({});
  const [dataTable, setDataTable] = useState([]);
  const [socialTaskTypes, setSocialTaskTypes] = useState([]);
  const isLocked = dataTable.some(item => item.isLocked);

  const onSearch = values => {
    setYearEduId(values.yearEduId);
    setSemesterId(values.semesterId);
  };

  const createDataReq = rowData => {
    // {
    //   studentId: 0,
    //   score: 0,
    //   note: 'string',
    // },
    return {
      [rowData.studentId]: {
        studentId: rowData.studentId,
        score: rowData.score,
        note: rowData.note,
        socialTaskTypeId: rowData.socialTaskTypeId,
        noOfSocialDay: rowData.noOfSocialDay,
      },
    };
  };

  const getDataTable = async () => {
    const [data, err] = await getClassTraningScore({ classId, practiceTypeId, yearEduId, semesterId });
    if (err) {
      return;
    }
    createDataReq(data);
    const initDataReq = data.data.items.reduce((obj, item) => {
      return {
        ...obj,
        ...createDataReq(item),
      };
    }, {});
    setDataTableReq(initDataReq);
    setDataTable(data.data.items);
  };

  const callGetSocialTaskTypes = async () => {
    const [data] = await getSocialTaskTypes();
    setSocialTaskTypes(data.data.items);
  };

  useEffect(() => {
    callGetSocialTaskTypes();
  }, []);

  useEffect(() => {
    if (!semesterId) {
      return;
    }
    getDataTable();
  }, [classId, practiceTypeId, yearEduId, semesterId]);

  const reloadTable = () => {
    getDataTable();
  };

  const callUpdate = async () => {
    const dataReq = {
      practiceTypeId,
      yearEduId,
      semesterId,
      students: Object.values(dataTableReq),
    };
    await updateClassTrainingScore(dataReq);
    message.success('Cập nhật thông tin thành công!');
    await reloadTable();
  };

  const callSubmit = async () => {
    const dataReq = {
      practiceTypeId,
      yearEduId,
      semesterId,
      students: Object.keys(dataTableReq),
    };
    await submitClassTrainingScore(dataReq);
    message.success('Khóa điểm thành công!');
    await reloadTable();
  };

  const handleSaveCellData = (rowData, { error, oldItem, form }) => {
    if (error) {
      setFormError(error);
      return null;
    }
    setFormError(null);
    const socialTaskType = socialTaskTypes.find(item => item.id === rowData.socialTaskTypeId);
    const oldSocialTaskType = socialTaskTypes.find(item => item.id === oldItem.socialTaskTypeId);
    if (
      rowData.socialTaskTypeId !== oldItem.socialTaskTypeId &&
      socialTaskType &&
      (!rowData.noOfSocialDay ||
        rowData.noOfSocialDay === '' ||
        (oldSocialTaskType && oldSocialTaskType.noOfDayCollected === rowData.noOfSocialDay))
    ) {
      rowData.noOfSocialDay = socialTaskType.noOfDayCollected;
      form.setFieldsValue({
        [`${rowData.studentCode}.noOfSocialDay`]: rowData.noOfSocialDay,
      });
    }
    setDataTableReq({
      ...dataTableReq,
      ...createDataReq(rowData),
    });
    return rowData;
  };

  const columns = [
    {
      title: 'Mã SV',
      dataIndex: 'studentCode',
      key: 'studentCode',
    },
    {
      title: 'Họ tên',
      dataIndex: 'fullname',
      key: 'fullname',
    },
    {
      title: 'Giới tính',
      dataIndex: 'genderText',
      key: 'genderText',
    },
    {
      title: 'Ngày sinh',
      dataIndex: 'dob',
      key: 'dob',
    },
    {
      title: 'Điểm',
      dataIndex: 'score',
      key: 'score',
      editable: !isLocked,
      alwayShowEdit: true,
      renderFormItemEdit: ({ form, record, onSave }) => (
        <Form.Item>
          {form.getFieldDecorator(`${record.studentCode}.score`, {
            initialValue: record.score || 0,
            rules: [requiredRule],
          })(<InputOnlyNumber style={{ width: 80 }} onPressEnter={onSave} onBlur={onSave} />)}
        </Form.Item>
      ),
    },
  ];

  if (practiceTypeId === 'CVHT') {
    columns.push(
      ...[
        {
          title: 'Loại CTXH',
          dataIndex: 'socialTaskTypeId',
          key: 'socialTaskTypeId',
          editable: !isLocked,
          alwayShowEdit: true,
          renderFormItemEdit: ({ form, record, onSave }) => (
            <Form.Item>
              {form.getFieldDecorator(`${record.studentCode}.socialTaskTypeId`, {
                initialValue: record.socialTaskTypeId || '',
                rules: [],
              })(
                <Select
                  params={{ socialTaskTypes }}
                  service={() => {
                    return { data: { items: socialTaskTypes } };
                  }}
                  style={{ width: 150 }}
                  onSelect={onSave}
                >
                  {socialTaskTypes.map(item => {
                    return <Select.Option value={item.id}>{item.label}</Select.Option>;
                  })}
                </Select>
              )}
            </Form.Item>
          ),
        },
        {
          title: 'Ngày CTXH',
          dataIndex: 'noOfSocialDay',
          key: 'noOfSocialDay',
          editable: !isLocked,
          alwayShowEdit: true,
          renderFormItemEdit: ({ form, record, onSave }) => (
            <Form.Item>
              {form.getFieldDecorator(`${record.studentCode}.noOfSocialDay`, {
                initialValue: record.noOfSocialDay || '',
                rules: [],
              })(<InputOnlyNumber style={{ width: 50 }} onPressEnter={onSave} onBlur={onSave} />)}
            </Form.Item>
          ),
        },
      ]
    );
  }
  columns.push({
    title: 'Ghi chú',
    dataIndex: 'note',
    key: 'note',
    editable: !isLocked,
    alwayShowEdit: true,
    renderFormItemEdit: ({ form, record, onSave }) => (
      <Form.Item>
        {form.getFieldDecorator(`${record.studentCode}.note`, {
          initialValue: record.note || '',
          rules: [],
        })(<Input style={{ width: 150 }} onPressEnter={onSave} onBlur={onSave} />)}
      </Form.Item>
    ),
  });
  return (
    <Card>
      <Row className="comp-traning-score">
        <Col span={24}>
          <FormSearch onSubmit={onSearch} />
        </Col>
        <Col span={24}>
          {!isLocked && dataTable.length > 0 && (
            <div style={{ textAlign: 'right' }}>
              <BtnGroup>
                <Btn disabled={!!formError} primary onClick={callUpdate}>
                  Lưu thông tin
                </Btn>
                <Btn primary disabled={!!formError} onClick={callSubmit}>
                  Khóa điểm
                </Btn>
              </BtnGroup>
            </div>
          )}
          <TableDataEditCell
            columns={columns}
            rowKey="studentCode"
            notPagination
            paramSearch={{ dataTable }}
            service={async () => {
              return [{ data: { items: dataTable } }];
            }}
            handleSaveCellData={handleSaveCellData}
          />
        </Col>
      </Row>
    </Card>
  );
};



